html,body{
    overflow-x: hidden;
    height: 100vh;
}

.slider-buttons{
    cursor: pointer;
    width: 50px;
    font-size: 50px;
    position: absolute;
    border-radius: 4px;
    text-align: center;
    box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
    background-color: #1976d2;
    color: #fff;
}

.MuiGrid-root{
    position: relative;
}

#increment{
    right: -60px;
}
#decrement{
    left: -40px;
}

.weather-average{
    justify-content: center;
    display: flex;
    align-items: center;
}
.weather-average span{
    color: #777;
    font-size: 1rem;
    margin-left: 5px;
}

.small-weather-image{
    max-width: 100%;
}

.text-center{
    text-align: center;
}

.hourly-temperature{
    font-size: .8rem;
}

.hourly-temperature-container{
    border-radius: 5px;
    padding: 5px 0;
    transition: background-color 200ms ease;
}

.hourly-temperature-container:hover{
    background-color: #efefef;
}

#spinner-glass{
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: rgba(255,255,255,0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    left: 0;
    top: 0;
}
@media all and (max-width: 1270px) and (min-width: 501px){
    #increment{
        right: -45px;
    }
    #decrement{
        left: -10px;
        z-index: 9;
    }
}

@media all and (max-width: 500px){
    #increment{
        right: -15px;
    }
    #decrement{
        left: 15px;
        z-index: 9;
    }

    h5{
        font-size: 1.4rem !important;
    }
}